$primary: #ff4f5a;
$secondary: #0a183d;
$sec_dark: #0a183d;
$headingColor: #ff6363;
$textColor: #1d164d;
$bgColor: #f2f5f9;
@mixin min($value) {
  @media screen and (min-width: ($value + "px")) {
    @content;
  }
}
@mixin max($value) {
  @media screen and (max-width: ($value + "px")) {
    @content;
  }
}
